/* site nav */

.site-nav {
  display: flex;
  align-items: center;
  margin-inline-start: auto;
}

.site-nav__item + .site-nav__item {
  margin-left: 35px;
}

.site-nav__link {
  position: relative;
  display: block;
  padding-top: 52px;
  padding-bottom: 52px;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  text-decoration: none;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  color: #ffffff;
  &:hover,
  &:focus {
    color: var(--ice-text-color);
  }
  &::after {
    position: absolute;
    left: 0;
    bottom: -1%;

    content: '';
    width: 100%;
    height: 4px;
    border-radius: 2px;
    // background-color: var(--accent-color);
    opacity: 0;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
}

.site-nav__link--current {
  color: #ffb8ca;
  &::after {
    opacity: 1;
  }
}

.mobile-menu__btn {
  display: flex;
  padding: 0;

  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  margin-left: auto;
  border: none;
  background-color: transparent;
  z-index: 10;

  cursor: pointer;

  &:hover > .mobile-menu__icon,
  &:focus > .mobile-menu__icon {
    fill: var(--accent-color);
  }

  @media screen and (min-width: 768px) {
    display: none;
  }
}

.mobile-menu__icon {
  transition: fill 2500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.mobile-menu__btn .icon__open {
  opacity: 1;
}
.mobile-menu__btn.is-open .icon__open {
  opacity: 0;
}

.mobile-menu__btn .icon__close {
  opacity: 0;
}

.mobile-menu__btn.is-open .icon__close {
  opacity: 1;
}

//mobile-menu//

.mobile-menu {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
  width: 100%;
  height: 100%;
  padding: 48px 40px;
  background-color: var(--accessorial-text-color);
  z-index: 1;

  @media screen and (max-width: 479px) {
    max-width: 479px;
  }

  @media screen and (min-width: 768px) {
    display: none;
  }
}

.mobile-menu.is-open {
  transform: translateX(0%);
}

.mobile-menu__link {
  font-family: 'DM Sans';
  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;
  letter-spacing: 0.02em;
  text-decoration: none;
  color: #ffffff;
  &:not(:last-child) {
    margin-bottom: 32px;

  }
}

